import { createSlice } from "@reduxjs/toolkit";

const initialState = [];



export const mainSlice = createSlice({
  name: "main",
  initialState,
  reducers: {
    shopaddCount(state, action) {
      //   id  id  
      let nums = state.findIndex((a) => a.id === action.payload);
      state[nums].count++;
      //   ++
      state[nums].money += state[nums].save;
    },
    shopminuseCount(state, action) {
      let nums = state.findIndex((a) => a.id === action.payload);
      if (state[nums].count > 1) {
        state[nums].count--;
        state[nums].money -= state[nums].save;
      }
    },
    shopdelete(state, action) {
      alert(".");
      return state.filter((a) => a.id !== action.payload);
    },
    shopAlldelete(state) {
      return (state = []);
    },
    shopcartAdd(state, action) {
      //     false 
      const someCheck = state.some((a) => a.id === action.payload.id);
      // console.log(someCheck);
      //   true   false
      //   false   true  if 
      if (!someCheck) {
        state.push(action.payload);
      } else {
        alert(" .");
      }
    },

    toggleCheckbox(state, action) {
      state.forEach((item) => {
        if (item.id === action.payload) {
          item.checked = !item.checked;
        }
      });
    },
    toggleCheckboxAll(state, action) {
      const isChecked = action.payload;
      state.forEach((item) => {
        item.checked = isChecked;
      });
    },

    deleteChecked(state) {
      return state.filter((item) => !item.checked);
    },

    // add: (state, action) => {
    //   const comment = state.comment.find((item) =>{
    //     item.id === action.payload.id
    //   })
    //   nextId++;
    //   state.push({
    //     id: nextId,
    //     pid: action.payload.pid, // pid
    //     name: action.payload.name,
    //     text: action.payload.text, // text 
    //   });
    //   console.log(nextId);
    //   console.log(comment)
    // },
  },
});

export const {
  shopaddCount,
  shopminuseCount,
  shopdelete,
  shopcartAdd,
  shoptextAdd,
  toggleCheckbox,
  deleteChecked,
  toggleCheckboxAll,
  shopAlldelete,
} = mainSlice.actions;

export default mainSlice.reducer;
