import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import '../css/Review.css'
import { add } from '../redux/comment';
import { addReview } from '../redux/review';

import { FaStar } from 'react-icons/fa';
import styled from 'styled-components';
import { purchaseBoolean } from '../redux/singup';


const Review = ({setShowReviewModal, showReviewModal}) => {

    let {id} = useParams();

    const navigate = useNavigate();

    const users = useSelector((state) => state.user);
    const sign = useSelector((state) => state.signup);
    const reviews = useSelector((state) => state.review);
    const shop = useSelector((state) => state);

    const date = new Date();
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, "0");
    const today = String(date.getDate()).padStart(2, "0");
    const [userDate, setUserDate] = useState(`${year}-${month}-${today}`);

    const dispatch = useDispatch();

    //       /   
    const findUser = sign.userlist.find((user)=> user.id == users.id)
    // useParams :id   id  
    const shoplist = shop.players.find(r=> r.id == id)

    const checkuser = findUser.purchaseArray.find((e)=> e.id == id)

    // 
    const starArray = [0, 1, 2, 3, 4];
    const [clicked, setClicked] = useState([false, false, false, false, false]);
    const [star, setStar] = useState(); 
    

      const [comment, setComment] = useState(
          {
              id :0,
              pid : id,
              name: findUser.name, //     
              text : "",
              date : userDate,
              rate : star   
          }
      )
  
      const handleText = (e) => {  //   .....
          setComment({...comment, text : e.target.value, rate : star})
      }
  
      const onReset = () => {
          setComment({...comment, text : ""})
      }
  
      const addtext = (e) =>{
          e.preventDefault()
          if(comment.text !== ""){
              dispatch(addReview(comment))
            //   dispatch(PidAdd(id))  redux 
              alert("  !")
              navigate(`/detailpage/${id}`)
          }else{
              alert(" ")
          }
          onReset();
          document.querySelector("input").value="";
          
      }

      const addboolean = () => {
        if(checkuser.id == id){
          dispatch(purchaseBoolean({...findUser}))
          console.log(checkuser)
        }
      }
      



    //     
    // Modal  useRef 
    const modalRef = useRef(null);
    
    useEffect(() => {
        //   
        const handler = (event) => {
            // mousedown      ,   
            if (modalRef.current && !modalRef.current.contains(event.target)) {
                setShowReviewModal(false);
            }
        };
        
        //   
        document.addEventListener('mousedown', handler);
        // document.addEventListener('touchstart', handler); //  
        
        return () => {
            //   
            document.removeEventListener('mousedown', handler);
            // document.removeEventListener('touchstart', handler); //  
        };
    });

   
    
    // 
    const handleStarClick = index => {
        let clickStates = [...clicked];
        for (let i = 0; i < 5; i++) {
          clickStates[i] = i <= index ? true : false;
        }
        setClicked(clickStates);
        console.log(clickStates)
      };

    useEffect(() => {
        sendReview();
    }, [clicked]); // 

  const sendReview = () => {
    let score = clicked.filter(Boolean).length;
    setStar(score)
    // fetch('http://52.78.63.175:8000/movie', {
    //   method: 'POST',
    //   Headers: {
    //     Authroization: 'e7f59ef4b4900fe5aa839fcbe7c5ceb7',
    //   },
    //   body: JSON.stringify({
    //     movie_id:1
    //     star: score,
    //   }),
    // });
  };


    return (  
        <div className='review-container'>
            <div className="review-modal" >
                <h1 className='review-title'>Review</h1>
                <p className='review-subtitle'> ?   !</p>
                <img src={shoplist.image} className="review-img"/>
                <Stars className='review-star'>
                        {
                            starArray.map((el, idx) => {
                            return (
                                <FaStar
                                  key={idx}
                                  size="30"
                                  onClick={() => handleStarClick(el)}
                                  className={clicked[el] && 'yellowStar'}
                                />
                            );
                            })
                        }
                </Stars>
                <form onSubmit={addtext}>
                    <textarea type="text" onChange={handleText}  className='review-input'/>
                    <button type='submit' onClick={addboolean} className='review-btn'> </button>
                </form>
            </div>
        </div>
    );
}

export default Review;

const Stars = styled.div`
  display: flex;
  padding-top: 5px;

  & svg {
    color: gray;
    cursor: pointer;
    margin-top: 10px;
    margin-bottom: 18px;
  }

  :hover svg {
    color: #fcc419;
  }

  & svg:hover ~ svg {
    color: gray;
  }

  .yellowStar {
    color: #fcc419;
  }
`;